import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

function getLink(name, link) {
  return (
    <span style={{ 'margin-right': '15px' }}><a href={link} target='_blank' rel="noopener noreferrer">{name}</a></span>
  )
}

function getArea(title) {
  return (
    <div style={{ 'margin-top': '20px' }}><strong>{title}</strong></div>
  )
}

const SecondPage = () => (
  <Layout>
    <SEO title="About" />
    <p>I'm a software developer learning how to use better NASA's data since my Spring class in 2017.
      <br />
      Today, I mentor IT subjects in a National Aeronautics and Space Administration program.
      <br />
      <a href='https://github.com/NASADatanauts/alexbelloni' target='_blank' rel="noopener noreferrer">github</a> |
      <a href='http://www.alexandrebelloni.com/home/?o=nasa' target='_blank' rel="noopener noreferrer">website</a>
    </p>

    <p>The NASA Datanaut Program is a community of software engineers and developers to engage with each other and subject matter experts to solve data
      challenges around NASAâ€™s data science initiatives such as deep learning, orbital dynamics, APIs, data visualization, and internet of things.
      <br />
      <a href='https://open.nasa.gov/explore/datanauts/' target='_blank' rel="noopener noreferrer">Program Page</a>
    </p>


    {getArea('Resources')}
    <div>
      {getLink('Socrata API Search', 'https://dev.socrata.com/data/')}
      {getLink('NASA APIs', 'https://api.nasa.gov/')}
      {getLink('CODE.NASA.GOV', 'https://code.nasa.gov/')}
      {getLink("NASA'S DATA PORTAL", 'https://data.nasa.gov/')}
      {getLink('Developer Resources Page', 'https://data.nasa.gov/stories/s/gk8h-th3y')}
      {getLink('Dataset Suggestions', 'https://data.nasa.gov/nominate')}
      {getLink('NASA LLIS', 'https://llis.nasa.gov/')}
      {getLink('TechPort', 'http://techport.nasa.gov/home')}
    </div>

    {getArea('Exclusive Area')}
    <div>
      {getLink('Datanauts private chat group', 'https://www.datanauts.chat/')}
      {getLink('Workspace', 'https://open.nasa.gov/explore/datanauts/app/')}
      {getLink('Calendar', 'https://open.nasa.gov/explore/datanauts/app/calendar')}
    </div>
    <br />
  </Layout>
)

export default SecondPage
